export default {
	data() {
		return {
			Height: '',
			stateBarHeight: '',
			attentionList: [[],[],[],[]],
			ctype: [],
			navIndex: 0
		}
	},
	onLoad() {	
		this.Height=this.$store.state.windowHeight
		this.stateBarHeight = this.$store.state.statusBarHeight
		let _this = this
		let openId=uni.getStorageSync('openid');
		uni.request({
			url:_this.$store.state.baseurl+'/careerType/getType',
			data:{
				openId:openId,
			},
			method:'GET',
			header : {'content-type':'application/x-www-form-urlencoded'},
			success:function(res){
				console.log(res)
				_this.ctype = res.data.data
					_this.attention(1)
			
			}
		})
	
	},
	methods:{
		toOrder:function(e){
			uni.navigateTo({
				url:'/components/bosspages/order?id='+e
			})
		},
		goback:function(){
		
			this.$store.commit('change_page',2)
			uni.navigateTo({
				url:'/pages/index/index'
			})
		},
		swiperChange:function(e){
			console.log(e.detail.current)
			let index = e.detail.current
			this.navClick(index)
		},
		attention: function(index){
			let openId=uni.getStorageSync('openid');
			let _this=this;
			//关注列表
			console.log(index)
			uni.request({
				url:_this.$store.state.baseurl+'/attention/list',
				data:{
					openId:openId,
					cid: index
				},
				method:'POST',
				header : {'content-type':'application/x-www-form-urlencoded'},
				success:function(res){
					console.log(res)
					var arr = _this.attentionList
					arr.splice(index-1,1,res.data.data);
					_this.attentionList = arr
					console.log(arr)
				}
			})
		},
		navClick: function(index){
			this.navIndex = index
			this.attention(index+1)
		},
	}

}
